"use client";

import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import FooterBar from "@/components/FooterBar";
// 通过 CDN 动态加载脚本
function loadScript(src: string): Promise<void> {
  return new Promise((resolve, reject) => {
    if (document.querySelector(`script[src="${src}"]`)) {
      resolve();
      return;
    }
    const s = document.createElement("script");
    s.src = src;
    s.async = true;
    s.onload = () => resolve();
    s.onerror = () => reject(new Error(`Failed to load ${src}`));
    document.body.appendChild(s);
  });
}

function ZodiacScene() {
  const containerRef = useRef<HTMLDivElement | null>(null);
  const labelContainerRef = useRef<HTMLDivElement | null>(null);
  const infoRef = useRef<HTMLDivElement | null>(null);
  const inputRef = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    let cleanup: (() => void) | undefined;

    const init = async () => {
      // 加载 THREE 及拓展
      await loadScript("https://unpkg.com/three@0.132.2/build/three.min.js");
      await loadScript(
        "https://cdn.jsdelivr.net/npm/three@0.132.2/examples/js/controls/OrbitControls.js"
      );
      await loadScript(
        "https://cdn.jsdelivr.net/npm/three@0.132.2/examples/js/renderers/CSS2DRenderer.js"
      );

      // @ts-ignore
      const THREE = (window as any).THREE;
      if (!THREE || !containerRef.current || !labelContainerRef.current) return;

      const scene = new THREE.Scene();
      const camera = new THREE.PerspectiveCamera(
        75,
        containerRef.current.clientWidth / containerRef.current.clientHeight,
        0.1,
        1000
      );
      const renderer = new THREE.WebGLRenderer({
        antialias: true,
        alpha: true,
      });
      renderer.setSize(
        containerRef.current.clientWidth,
        containerRef.current.clientHeight
      );
      renderer.setClearColor(0x000000, 1);
      containerRef.current.appendChild(renderer.domElement);

      // @ts-ignore
      const labelRenderer = new THREE.CSS2DRenderer();
      labelRenderer.setSize(
        containerRef.current.clientWidth,
        containerRef.current.clientHeight
      );
      labelRenderer.domElement.style.position = "absolute";
      labelRenderer.domElement.style.top = "0";
      labelContainerRef.current.appendChild(labelRenderer.domElement);

      // 星空
      const starsGeometry = new THREE.BufferGeometry();
      const starsCount = 1200;
      const positions = new Float32Array(starsCount * 3);
      for (let i = 0; i < starsCount * 3; i += 3) {
        positions[i] = (Math.random() - 0.5) * 100;
        positions[i + 1] = (Math.random() - 0.5) * 100;
        positions[i + 2] = (Math.random() - 0.5) * 100;
      }
      starsGeometry.setAttribute(
        "position",
        new THREE.BufferAttribute(positions, 3)
      );
      const starsMaterial = new THREE.PointsMaterial({
        color: 0xffffff,
        size: 0.08,
        transparent: true,
        opacity: 0.9,
        sizeAttenuation: true,
      });
      const starField = new THREE.Points(starsGeometry, starsMaterial);
      scene.add(starField);

      // 控制器
      // @ts-ignore
      const controls = new THREE.OrbitControls(
        camera,
        labelRenderer.domElement
      );
      controls.enableDamping = true;

      // 太阳
      const sunGeometry = new THREE.SphereGeometry(0.2, 32, 32);
      const sunMaterial = new THREE.MeshBasicMaterial({ color: 0xffc107 });
      const sun = new THREE.Mesh(sunGeometry, sunMaterial);
      sun.position.set(0, 0, 0);
      scene.add(sun);

      // 地球
      const earthGeometry = new THREE.SphereGeometry(0.1, 32, 32);
      const earthMaterial = new THREE.MeshBasicMaterial({ color: 0x3aa7ff });
      const earth = new THREE.Mesh(earthGeometry, earthMaterial);
      scene.add(earth);

      // 地球到太阳的虚线
      const lineMaterial = new THREE.LineDashedMaterial({
        color: 0xffffff,
        dashSize: 0.2,
        gapSize: 0.1,
      });
      const lineGeometry = new THREE.BufferGeometry();
      const linePositions = new Float32Array(6);
      lineGeometry.setAttribute(
        "position",
        new THREE.BufferAttribute(linePositions, 3)
      );
      const earthSunLine = new THREE.Line(lineGeometry, lineMaterial);
      earthSunLine.computeLineDistances();
      scene.add(earthSunLine);

      // 轨道与黄道
      const orbit = new THREE.Mesh(
        new THREE.TorusGeometry(1, 0.007, 16, 200),
        new THREE.MeshBasicMaterial({
          color: 0x4169e1,
          transparent: true,
          opacity: 0.8,
        })
      );
      scene.add(orbit);
      const orbitGlow = new THREE.Mesh(
        new THREE.TorusGeometry(1, 0.02, 16, 200),
        new THREE.MeshBasicMaterial({
          color: 0x4169e1,
          transparent: true,
          opacity: 0.2,
        })
      );
      scene.add(orbitGlow);
      const ecliptic = new THREE.Mesh(
        new THREE.TorusGeometry(1.5, 0.007, 16, 200),
        new THREE.MeshBasicMaterial({
          color: 0xffd700,
          transparent: true,
          opacity: 0.8,
        })
      );
      scene.add(ecliptic);
      const eclipticGlow = new THREE.Mesh(
        new THREE.TorusGeometry(1.5, 0.02, 16, 200),
        new THREE.MeshBasicMaterial({
          color: 0xffd700,
          transparent: true,
          opacity: 0.2,
        })
      );
      scene.add(eclipticGlow);

      // 黄道十二宫中文
      const zodiacNames = [
        "天秤宫",
        "天蝎宫",
        "射手宫",
        "摩羯宫",
        "水瓶宫",
        "双鱼宫",
        "白羊宫",
        "金牛宫",
        "双子宫",
        "巨蟹宫",
        "狮子宫",
        "处女宫",
      ];
      for (let i = 0; i < 12; i++) {
        const angle = (i * Math.PI) / 6;
        const x = 1.5 * Math.cos(angle);
        const y = 1.5 * Math.sin(angle);
        const div = document.createElement("div");
        div.className = "label";
        div.textContent = zodiacNames[i];
        // @ts-ignore
        const label = new THREE.CSS2DObject(div);
        label.position.set(x * 1.1, y * 1.1, 0);
        scene.add(label);
      }

      // 二分二至点
      const seasonPoints = [
        { name: "春分", angle: 0, color: 0x00ff00, glowColor: 0x66ff66 },
        {
          name: "夏至",
          angle: Math.PI / 2,
          color: 0xff6666,
          glowColor: 0xff9999,
        },
        { name: "秋分", angle: Math.PI, color: 0xffcc00, glowColor: 0xffeb99 },
        {
          name: "冬至",
          angle: (3 * Math.PI) / 2,
          color: 0x99ccff,
          glowColor: 0xccebff,
        },
      ];
      seasonPoints.forEach((p: any) => {
        const core = new THREE.Mesh(
          new THREE.SphereGeometry(0.06, 32, 32),
          new THREE.MeshBasicMaterial({
            color: p.color,
            transparent: true,
            opacity: 1,
          })
        );
        core.position.set(Math.cos(p.angle), Math.sin(p.angle), 0);
        scene.add(core);
        const inner = new THREE.Mesh(
          new THREE.SphereGeometry(0.08, 32, 32),
          new THREE.MeshBasicMaterial({
            color: p.glowColor,
            transparent: true,
            opacity: 0.6,
          })
        );
        inner.position.copy(core.position);
        scene.add(inner);
        const middle = new THREE.Mesh(
          new THREE.SphereGeometry(0.1, 32, 32),
          new THREE.MeshBasicMaterial({
            color: p.glowColor,
            transparent: true,
            opacity: 0.3,
          })
        );
        middle.position.copy(core.position);
        scene.add(middle);
        const outer = new THREE.Mesh(
          new THREE.SphereGeometry(0.12, 32, 32),
          new THREE.MeshBasicMaterial({
            color: p.glowColor,
            transparent: true,
            opacity: 0.1,
          })
        );
        outer.position.copy(core.position);
        scene.add(outer);
        const div = document.createElement("div");
        div.className = "label";
        div.textContent = p.name;
        // @ts-ignore
        const label = new THREE.CSS2DObject(div);
        label.position.set(Math.cos(p.angle) * 1.2, Math.sin(p.angle) * 1.2, 0);
        scene.add(label);
      });

      // 相机
      camera.position.set(0, 0, 5);
      camera.lookAt(0, 0, 0);

      // 日期计算
      const julianDate = (date: Date) => {
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        const a = Math.floor((14 - month) / 12);
        const y = year + 4800 - a;
        const m = month + 12 * a - 3;
        return (
          day +
          Math.floor((153 * m + 2) / 5) +
          365 * y +
          Math.floor(y / 4) -
          Math.floor(y / 100) +
          Math.floor(y / 400) -
          32045 +
          0.5
        );
      };
      const calculatePositions = (date: Date) => {
        const JD = julianDate(date);
        const J2000 = 2451545.0;
        const n = JD - J2000;
        const L0 = (280.46646 + 0.98564736 * n) % 360;
        const M = (357.52911 + 0.98560028 * n) % 360;
        const Mr = (M * Math.PI) / 180;
        const C =
          (1.914602 -
            0.004817 * (n / 36525) -
            0.000014 * Math.pow(n / 36525, 2)) *
            Math.sin(Mr) +
          (0.019993 - 0.000101 * (n / 36525)) * Math.sin(2 * Mr) +
          0.000289 * Math.sin(3 * Mr);
        const LE = (L0 + C) % 360;
        const LS = (LE + 180) % 360;
        return {
          earthLongitude: LE,
          sunLongitude: LS,
          earthX: Math.cos((LE * Math.PI) / 180),
          earthY: Math.sin((LE * Math.PI) / 180),
        };
      };

      // 日期 UI 初始化
      const setToday = () => {
        const now = new Date();
        const year = now.getFullYear();
        const month = String(now.getMonth() + 1).padStart(2, "0");
        const day = String(now.getDate()).padStart(2, "0");
        const hours = String(now.getHours()).padStart(2, "0");
        const minutes = String(now.getMinutes()).padStart(2, "0");
        if (inputRef.current)
          inputRef.current.value = `${year}-${month}-${day}T${hours}:${minutes}`;
      };

      let currentDate = new Date();
      setToday();

      const updatePositions = () => {
        const pos = calculatePositions(currentDate);
        earth.position.set(pos.earthX, pos.earthY, 0);
        const sunDir = new THREE.Vector3(
          -pos.earthX,
          -pos.earthY,
          0
        ).normalize();
        const extended = sunDir.multiplyScalar(3);
        const arr = earthSunLine.geometry.attributes.position
          .array as Float32Array;
        arr[0] = pos.earthX;
        arr[1] = pos.earthY;
        arr[2] = 0;
        arr[3] = extended.x;
        arr[4] = extended.y;
        arr[5] = 0;
        earthSunLine.geometry.attributes.position.needsUpdate = true;
        earthSunLine.computeLineDistances();
        const zodiacIndex = Math.floor(pos.sunLongitude / 30);
        const currentZodiac = [
          "天秤宫",
          "天蝎宫",
          "射手宫",
          "摩羯宫",
          "水瓶宫",
          "双鱼宫",
          "白羊宫",
          "金牛宫",
          "双子宫",
          "巨蟹宫",
          "狮子宫",
          "处女宫",
        ][zodiacIndex];
        if (infoRef.current) {
          infoRef.current.textContent = `观测日期: ${currentDate.toLocaleDateString()}\n地球的黄经: ${pos.earthLongitude.toFixed(
            2
          )}°\n太阳的视黄经: ${pos.sunLongitude.toFixed(
            2
          )}°\n太阳位于: ${currentZodiac}`;
        }
      };

      inputRef.current?.addEventListener("change", () => {
        if (inputRef.current?.value)
          currentDate = new Date(inputRef.current.value);
        updatePositions();
      });

      const onResize = () => {
        if (!containerRef.current) return;
        camera.aspect =
          containerRef.current.clientWidth / containerRef.current.clientHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(
          containerRef.current.clientWidth,
          containerRef.current.clientHeight
        );
        labelRenderer.setSize(
          containerRef.current.clientWidth,
          containerRef.current.clientHeight
        );
      };
      window.addEventListener("resize", onResize);

      camera.position.set(0, 0, 5);
      camera.lookAt(0, 0, 0);
      updatePositions();

      let raf = 0;
      const animate = () => {
        raf = requestAnimationFrame(animate);
        controls.update();
        renderer.render(scene, camera);
        labelRenderer.render(scene, camera);
      };
      animate();

      cleanup = () => {
        cancelAnimationFrame(raf);
        window.removeEventListener("resize", onResize);
        try {
          containerRef.current?.removeChild(renderer.domElement);
          labelContainerRef.current?.removeChild(labelRenderer.domElement);
        } catch {}
      };
    };

    init();
    return () => {
      cleanup && cleanup();
    };
  }, []);

  return (
    <div className="flex-1 relative">
      {/* 信息与日期选择 */}
      <div
        ref={infoRef}
        id="info"
        className="absolute left-2 top-2 text-yellow-300 text-xs font-mono whitespace-pre z-10"
      ></div>
      <div className="absolute right-2 top-2 bg-black/60 p-3 rounded-lg z-10">
        <label className="block text-white text-xs mb-1">选择日期：</label>
        <input
          ref={inputRef}
          type="datetime-local"
          className="px-2 py-1 rounded-md bg-white/90 text-black text-sm"
        />
      </div>
      <div ref={labelContainerRef} className="absolute inset-0" />
      <div ref={containerRef} className="absolute inset-0" />
      <style jsx>{`
        .label {
          color: #fff;
          font-family: sans-serif;
          padding: 2px 4px;
          background: rgba(0, 0, 0, 0.6);
          border-radius: 4px;
        }
      `}</style>
    </div>
  );
}

function SevenStarsScene() {
  const containerRef = useRef<HTMLDivElement | null>(null);
  const overlayRef = useRef<HTMLDivElement | null>(null);
  const infoRef = useRef<HTMLDivElement | null>(null);
  const [hemisphere, setHemisphere] = useState<"north" | "south">("north");
  const [latitude, setLatitude] = useState<number>(35);
  const [year, setYear] = useState<number>(2024);
  const [month, setMonth] = useState<number>(6);
  const [day, setDay] = useState<number>(21);

  useEffect(() => {
    let cleanup: (() => void) | undefined;
    const init = async () => {
      await loadScript("https://unpkg.com/three@0.132.2/build/three.min.js");
      await loadScript(
        "https://cdn.jsdelivr.net/npm/three@0.132.2/examples/js/controls/OrbitControls.js"
      );
      // @ts-ignore
      const THREE = (window as any).THREE;
      if (!THREE || !containerRef.current || !overlayRef.current) return;

      const scene = new THREE.Scene();
      scene.background = new THREE.Color(0x000000);
      const camera = new THREE.PerspectiveCamera(
        75,
        containerRef.current.clientWidth / containerRef.current.clientHeight,
        0.1,
        1000
      );
      const renderer = new THREE.WebGLRenderer({
        antialias: true,
        alpha: true,
      });
      renderer.setSize(
        containerRef.current.clientWidth,
        containerRef.current.clientHeight
      );
      containerRef.current.appendChild(renderer.domElement);

      // @ts-ignore
      const controls = new THREE.OrbitControls(camera, renderer.domElement);
      camera.position.set(4, 4, 4);
      controls.update();

      const radius = 2;
      const segments = 30;
      const sphere = new THREE.Mesh(
        new THREE.SphereGeometry(radius, segments, segments),
        new THREE.MeshBasicMaterial({
          color: 0xffffff,
          wireframe: true,
          opacity: 0.1,
          transparent: true,
        })
      );
      scene.add(sphere);
      const equatorialPlane = new THREE.Mesh(
        new THREE.CircleGeometry(radius, segments),
        new THREE.MeshBasicMaterial({
          color: 0x4169e1,
          transparent: true,
          opacity: 0.2,
          side: THREE.DoubleSide,
        })
      );
      equatorialPlane.rotation.x = Math.PI / 2;
      scene.add(equatorialPlane);
      const equatorLine = new THREE.Mesh(
        new THREE.TorusGeometry(radius, 0.003, 16, 100),
        new THREE.MeshBasicMaterial({ color: 0xffffff })
      );
      equatorLine.rotation.x = Math.PI / 2;
      scene.add(equatorLine);
      scene.add(new THREE.AxesHelper(radius * 1.2));

      // 方位点与标签
      const orientation = [
        "东",
        "东北",
        "北",
        "西北",
        "西",
        "西南",
        "南",
        "东南",
      ];
      const orientPts: any[] = [];
      orientation.forEach((name, i) => {
        const angle = (i * Math.PI * 2) / 8;
        const x = radius * Math.cos(angle);
        const z = radius * Math.sin(angle);
        const y = 0;
        const point = new THREE.Mesh(
          new THREE.SphereGeometry(0.02),
          new THREE.MeshBasicMaterial({ color: 0x00ffff })
        );
        point.position.set(x, y, z);
        scene.add(point);
        const label = document.createElement("div");
        label.className = "label";
        label.textContent = name;
        label.style.position = "absolute";
        label.style.color = "white";
        label.style.padding = "2px";
        label.style.backgroundColor = "rgba(0,0,0,0.5)";
        overlayRef.current!.appendChild(label);
        orientPts.push({ point, label });
      });

      let objectsToRemove: any[] = [];
      let northPoleLabel: HTMLDivElement | null = null;
      let southPoleLabel: HTMLDivElement | null = null;

      const rebuild = () => {
        objectsToRemove.forEach((obj) => {
          try {
            scene.remove(obj);
            obj.geometry?.dispose?.();
            obj.material?.dispose?.();
          } catch {}
        });
        objectsToRemove = [];

        // 清除之前的极点标签
        if (northPoleLabel) {
          northPoleLabel.remove();
          northPoleLabel = null;
        }
        if (southPoleLabel) {
          southPoleLabel.remove();
          southPoleLabel = null;
        }

        const lat = Math.abs(latitude) * (hemisphere === "north" ? 1 : -1);
        const delta =
          (23.45 *
            Math.sin((2 * Math.PI * (284 + (month - 1) * 30 + day)) / 365) *
            Math.PI) /
          180;
        const latitudeRad = (lat * Math.PI) / 180;

        // 计算北天极点位置
        const northPole = new THREE.Vector3(
          0,
          Math.sin(latitudeRad) * radius * 1.2,
          Math.cos(latitudeRad) * radius * 1.2
        );
        const southPole = northPole.clone().multiplyScalar(-1);

        const poleGeom = new THREE.SphereGeometry(0.03);
        const poleMat = new THREE.MeshBasicMaterial({ color: 0xffffff });
        const northPoleMark = new THREE.Mesh(poleGeom, poleMat);
        northPoleMark.position.copy(northPole);
        scene.add(northPoleMark);
        const southPoleMark = new THREE.Mesh(poleGeom, poleMat);
        southPoleMark.position.copy(southPole);
        scene.add(southPoleMark);

        // 天轴
        const axis = new THREE.Line(
          new THREE.BufferGeometry().setFromPoints([northPole, southPole]),
          new THREE.LineDashedMaterial({
            color: 0xff0000,
            dashSize: 0.1,
            gapSize: 0.05,
          })
        );
        axis.computeLineDistances();
        scene.add(axis);
        objectsToRemove.push(northPoleMark, southPoleMark, axis);

        // 创建极点标签
        northPoleLabel = document.createElement("div");
        northPoleLabel.className = "label";
        northPoleLabel.style.position = "absolute";
        northPoleLabel.style.color = "white";
        northPoleLabel.style.padding = "2px";
        northPoleLabel.style.backgroundColor = "rgba(0,0,0,0.5)";
        northPoleLabel.textContent = "北天极";
        overlayRef.current!.appendChild(northPoleLabel);

        southPoleLabel = document.createElement("div");
        southPoleLabel.className = "label";
        southPoleLabel.style.position = "absolute";
        southPoleLabel.style.color = "white";
        southPoleLabel.style.padding = "2px";
        southPoleLabel.style.backgroundColor = "rgba(0,0,0,0.5)";
        southPoleLabel.textContent = "南天极";
        overlayRef.current!.appendChild(southPoleLabel);

        // 太阳轨迹（依赤纬）
        const points: any[] = [];
        const timePoints = 100;
        for (let i = 0; i < timePoints; i++) {
          const t = (i / (timePoints - 1)) * Math.PI * 2;
          const d = radius * Math.sin(delta);
          const circleR = Math.sqrt(radius * radius - d * d);
          const x = circleR * Math.cos(t);
          const y = circleR * Math.sin(t);
          const z = d;
          const zAxis = new THREE.Vector3(0, 0, 1);
          const northPoleDir = new THREE.Vector3(
            0,
            Math.sin(latitudeRad),
            Math.cos(latitudeRad)
          ).normalize();
          const rotationAxis = new THREE.Vector3()
            .crossVectors(zAxis, northPoleDir)
            .normalize();
          const rotationAngle = Math.acos(zAxis.dot(northPoleDir));
          const rotationMatrix = new THREE.Matrix4().makeRotationAxis(
            rotationAxis,
            rotationAngle
          );
          const p = new THREE.Vector3(x, y, z).applyMatrix4(rotationMatrix);
          points.push(p);
        }
        const curve = new THREE.CatmullRomCurve3(points);
        const sunPath = new THREE.Mesh(
          new THREE.TubeGeometry(curve, 50, 0.01, 20, false),
          new THREE.MeshBasicMaterial({ color: 0xffff00 })
        );
        scene.add(sunPath);
        objectsToRemove.push(sunPath);

        // 计算日长、日出日落
        const tanDelta = Math.tan(delta);
        const tanLat = Math.tan(latitudeRad);
        const product = -tanDelta * tanLat;
        let Ts = 0;
        let sunrise = "--:--";
        let sunset = "--:--";
        let isExtreme = false;
        if (product > 1) {
          Ts = 0;
          isExtreme = true;
        } else if (product < -1) {
          Ts = 24;
          isExtreme = true;
        } else {
          Ts = (2 / 15) * Math.acos(product) * (180 / Math.PI);
          const Tc = 12;
          const r1 = Tc - Ts / 2;
          const r2 = Tc + Ts / 2;
          sunrise = `${Math.floor(r1)}:${String(
            Math.floor((r1 % 1) * 60)
          ).padStart(2, "0")}`;
          sunset = `${Math.floor(r2)}:${String(
            Math.floor((r2 % 1) * 60)
          ).padStart(2, "0")}`;
        }
        if (infoRef.current) {
          infoRef.current.innerHTML = `太阳周日视运动可视化<br/>观测位置：${
            hemisphere === "north" ? "北纬" : "南纬"
          } ${Math.abs(
            lat
          )}°<br/>观测日期：${year}年${month}月${day}日<br/>赤纬角：${(
            (delta * 180) /
            Math.PI
          ).toFixed(2)}°<br/>日照时长：${
            isExtreme
              ? Ts === 24
                ? "24小时0分钟"
                : "0小时0分钟"
              : `${Math.floor(Ts)}小时${Math.floor((Ts % 1) * 60)}分钟`
          }<br/>日出时间：${
            isExtreme ? (Ts === 24 ? "极昼" : "极夜") : sunrise
          }<br/>日落时间：${
            isExtreme ? (Ts === 24 ? "极昼" : "极夜") : sunset
          }`;
        }
      };

      const onResize = () => {
        if (!containerRef.current) return;
        camera.aspect =
          containerRef.current.clientWidth / containerRef.current.clientHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(
          containerRef.current.clientWidth,
          containerRef.current.clientHeight
        );
      };
      window.addEventListener("resize", onResize);

      let raf = 0;
      const animate = () => {
        raf = requestAnimationFrame(animate);
        controls.update();
        // 更新方位标签位置
        orientPts.forEach(({ point, label }) => {
          const vector = point.position.clone().project(camera);
          const x2d =
            (vector.x * 0.5 + 0.5) * (containerRef.current?.clientWidth || 0);
          const y2d =
            (-vector.y * 0.5 + 0.5) * (containerRef.current?.clientHeight || 0);
          label.style.left = `${x2d}px`;
          label.style.top = `${y2d}px`;
        });

        // 更新极点标签位置
        if (northPoleLabel && southPoleLabel) {
          const northPole = objectsToRemove.find(
            (obj) => obj.position && obj.position.y > 0
          );
          const southPole = objectsToRemove.find(
            (obj) => obj.position && obj.position.y < 0
          );

          if (northPole) {
            const vector = northPole.position.clone().project(camera);
            const x2d =
              (vector.x * 0.5 + 0.5) * (containerRef.current?.clientWidth || 0);
            const y2d =
              (-vector.y * 0.5 + 0.5) *
              (containerRef.current?.clientHeight || 0);
            northPoleLabel.style.left = `${x2d}px`;
            northPoleLabel.style.top = `${y2d}px`;
          }

          if (southPole) {
            const vector = southPole.position.clone().project(camera);
            const x2d =
              (vector.x * 0.5 + 0.5) * (containerRef.current?.clientWidth || 0);
            const y2d =
              (-vector.y * 0.5 + 0.5) *
              (containerRef.current?.clientHeight || 0);
            southPoleLabel.style.left = `${x2d}px`;
            southPoleLabel.style.top = `${y2d}px`;
          }
        }

        renderer.render(scene, camera);
      };

      rebuild();
      animate();

      cleanup = () => {
        cancelAnimationFrame(raf);
        window.removeEventListener("resize", onResize);
        try {
          containerRef.current?.removeChild(renderer.domElement);
        } catch {}
        overlayRef.current
          ?.querySelectorAll(".label")
          .forEach((n) => n.remove());
      };
    };

    init();
    return () => {
      cleanup && cleanup();
    };
  }, [hemisphere, latitude, year, month, day]);

  return (
    <div className="flex-1 relative">
      {/* 输入面板 */}
      <div className="absolute left-2 top-2 bg-black/60 text-white p-3 rounded z-10">
        <div className="mb-2">
          <label className="mr-2 text-xs">半球</label>
          <select
            className="bg-black/40 border border-white/20 rounded px-2 py-1 text-xs"
            value={hemisphere}
            onChange={(e) => setHemisphere(e.target.value as any)}
          >
            <option value="north">北半球</option>
            <option value="south">南半球</option>
          </select>
        </div>
        <div className="mb-2">
          <label className="mr-2 text-xs">纬度</label>
          <input
            className="w-16 bg-black/40 border border-white/20 rounded px-2 py-1 text-xs"
            type="number"
            min={0}
            max={90}
            step={0.1}
            value={latitude}
            onChange={(e) => setLatitude(Number(e.target.value))}
          />
        </div>
        <div className="flex space-x-2 items-center">
          <input
            className="w-16 bg-black/40 border border-white/20 rounded px-2 py-1 text-xs"
            type="number"
            value={year}
            onChange={(e) => setYear(Number(e.target.value))}
          />
          <input
            className="w-12 bg-black/40 border border-white/20 rounded px-2 py-1 text-xs"
            type="number"
            min={1}
            max={12}
            value={month}
            onChange={(e) => setMonth(Number(e.target.value))}
          />
          <input
            className="w-12 bg-black/40 border border-white/20 rounded px-2 py-1 text-xs"
            type="number"
            min={1}
            max={31}
            value={day}
            onChange={(e) => setDay(Number(e.target.value))}
          />
        </div>
        <button
          className="mt-2 px-3 py-1 bg-blue-600 text-white text-xs rounded hover:bg-blue-700 transition-colors"
          onClick={() => {
            // Trigger rebuild by updating state
            setLatitude(latitude);
          }}
        >
          更新
        </button>
      </div>
      {/* 信息面板 */}
      <div
        ref={infoRef}
        id="info"
        className="absolute right-2 top-20 bg-black/50 text-white p-3 rounded text-xs z-10"
      ></div>
      {/* 覆盖层放置标签 */}
      <div ref={overlayRef} className="absolute inset-0 pointer-events-none" />
      {/* three 容器 */}
      <div ref={containerRef} className="absolute inset-0" />
      <style jsx>{`
        .label {
          padding: 2px 4px;
          background: rgba(0, 0, 0, 0.5);
          border-radius: 4px;
          color: #fff;
          font-size: 12px;
        }
      `}</style>
    </div>
  );
}

function TibetanCyclicYear() {
  const [year, setYear] = useState<number>(2025);
  const [result, setResult] = useState<string>("");

  const [rotation, setRotation] = useState({ tg: 0, dz: 0 }); // 旋转角度

  const tiangan = ["铁", "铁", "水", "水", "木", "木", "火", "火", "土", "土"];
  const dizhi = [
    "虎",
    "兔",
    "龙",
    "蛇",
    "马",
    "羊",
    "猴",
    "鸡",
    "狗",
    "猪",
    "鼠",
    "牛",
  ];

  const ganzhiList: string[] = [];
  for (let i = 0; i < 60; i++) {
    ganzhiList.push(tiangan[(i - 6 + 10) % 10] + dizhi[(i - 2 + 12) % 12]);
  }

  const calculate = () => {
    if (!Number.isFinite(year)) return;
    const index = (year + 56 + 60) % 60;
    const tgIndex = index % 10;
    const dzIndex = index % 12;
    const tgAngle = tgIndex * 36 - 54; // 360/10
    const dzAngle = dzIndex * 30 + 15; // 360/12

    // 多转几圈再停
    setRotation({
      tg: rotation.tg + 360 * 3 + -tgAngle,
      dz: rotation.dz + 360 * 3 + -dzAngle,
    });

    setResult(`${year} 年是 【${ganzhiList[index]}】年`);
  };

  useEffect(() => {
    calculate(); // 初始化
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="flex-1 flex items-center justify-center p-4">
      <div className="w-full max-w-[800px] flex flex-col items-center">
        <h2 className="text-2xl font-bold mb-4">藏历“绕迥”纪年</h2>

        <div className="relative" style={{ width: 400, height: 400 }}>
          {/* 地支图层 */}
          <div
            className="absolute"
            style={{
              top: "25%",
              left: 0,
              width: 400,
              height: 400,
              padding: 30,
              transformOrigin: "center center",
              transform: `rotate(${rotation.dz}deg)`,
              transition: "transform 3s ease-out",
            }}
          >
            <img
              src="/pho/dizhi.png"
              alt="地支盘"
              className="w-full h-full select-none pointer-events-none"
            />
          </div>

          {/* 天干图层 */}
          <div
            className="absolute"
            style={{
              top: "25%",
              left: 0,
              width: 400,
              height: 400,
              padding: 30,
              transformOrigin: "center center",
              transform: `rotate(${rotation.tg}deg)`,
              transition: "transform 3s ease-out",
            }}
          >
            <img
              src="/pho/tiangan.png"
              alt="天干盘"
              className="w-full h-full select-none pointer-events-none"
            />
          </div>

          {/* 指针图层 */}
          <div
            className="absolute"
            style={{
              top: "25%",
              left: 0,
              width: 400,
              height: 400,
              padding: 30,
              transformOrigin: "center center",
            }}
          >
            <img
              src="/pho/needle.png"
              alt="指针"
              className="w-full h-full select-none pointer-events-none"
            />
          </div>
        </div>

        <div className="flex items-center mt-6">
          <input
            type="number"
            value={year}
            onChange={(e) => setYear(Number(e.target.value))}
            className="text-black rounded border border-white px-3 py-2 h-9 w-40"
            placeholder="输入年份"
          />
          <button
            onClick={calculate}
            className="ml-3 px-4 py-2 bg-fuchsia-700 rounded text-white h-9 flex items-center justify-center"
          >
            计算
          </button>
        </div>

        <div className="mt-4 text-lg h-[30px] text-center w-full">{result}</div>
      </div>
    </div>
  );
}
